<script>
import arrow from "@/components/arrow.vue";

export default {
  name: "boutique",
  components: {arrow},
  data() {
    return {
      heightList: [
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (1).jpg"),
          title: 0
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (2).jpg"),
          title: 1
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (3).jpg"),
          title: 2
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (4).jpg"),
          title: 3
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (5).jpg"),
          title: 4
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (6).jpg"),
          title: 5
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/height (7).jpg"),
          title: 6
        }
      ],
      widthList: [
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/width (1).jpg"),
          title: 0
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/width (2).jpg"),
          title: 1
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/width (3).jpg"),
          title: 2
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/width (4).jpg"),
          title: 3
        },
        {
          img: require("@/assets/images/门店简介配图/猫屎咖啡家族精品店/width (5).jpg"),
          title: 4
        }
      ],
      hWidth: 0,
      wWidth: 0,
      curHTranslateX: 0,
      curWTranslateX: 0,
      heightHover: false,
      widthHover: false,
    }
  },
  methods: {
    heightMove(num) {
      this.curHTranslateX += num;
      this.$refs.heightBoxRef.style.transform = `translateX(${this.curHTranslateX}px)`;
    },
    widthMove(num) {
      this.curWTranslateX += num;
      this.$refs.widthBoxRef.style.transform = `translateX(${this.curWTranslateX}px)`;
    }
  },
  mounted() {
    const heightRef = this.$refs.heightRef;
    this.hWidth = heightRef[0].offsetWidth;

    const widthRef = this.$refs.widthRef;
    this.wWidth = widthRef[0].offsetWidth;
  }
}
</script>

<template>
  <div>
    <div class="w-screen bg-coffeePink h-[80vh]">
      <div class="container mx-auto h-full flex items-center py-20">
        <div class="w-1/2 flex flex-col justify-center">
          <p class="font-bold text-4xl text-coffee">Kafelaku coffee family <br/> boutique shop</p>
          <p class="text-sm text-coffee w-3/4 mt-2 leading-6">Since the opening of the first store in Guangzhou in 2011, civet
            coffee
            has always adhered to the
            concept of excellence. From the planting, picking, and roasting of coffee beans to the processing and
            production of finished products, each link has been strictly controlled. With its unique coffee flavor and
            fine manufacturing technology, it quickly won the favor of consumers. The number of civet coffee stores
            has grown rapidly. As of 2024, it has been in 22 provinces and cities across the country. Including the
            sales of store opening and coffee derivatives. By creating different styles such as flash delivery stores,
            flagship stores, and family boutique stores, it provides consumers with a unique space experience.</p>
        </div>
        <div class="w-1/2 mt-10">
          <img class="w-3/4" src="@/assets/p.png" alt="">
        </div>
      </div>
    </div>
    <div class="w-full bg-white my-20">
      <div class="container mx-auto h-full">
        <div class="w-full relative overflow-x-auto hidden-scrollbar" @mousemove="heightHover = true"
             @mouseleave="heightHover = false">
          <div class="flex w-full h-102 transition-all" ref="heightBoxRef">
            <div v-for="item in heightList" :key="item.id" class="w-1/4 px-2 box-border h-full flex-shrink-0"
                 ref="heightRef">
              <img class="w-full h-full" :src="item.img" alt="">
            </div>
          </div>
          <arrow :hover="heightHover" :hidden-right="curHTranslateX === -((heightList.length - 4) *hWidth)"
                 :hidden-left="curHTranslateX ===0"
                 @left-click="heightMove(hWidth)"
                 @right-click="heightMove(-hWidth)"/>
        </div>
        <div class="font-bold text-coffee text-4xl text-center py-10">
          Kafelaku coffee family boutique shop
        </div>
        <div class="w-full flex relative overflow-x-auto hidden-scrollbar" @mousemove="widthHover = true"
             @mouseleave="widthHover = false">
          <div class="flex h-102 w-full transition-all" ref="widthBoxRef">
            <div v-for="item in widthList" :key="item.id" class="w-1/2 px-2 box-border h-full flex-shrink-0"
                 ref="widthRef">
              <img class="w-full h-full" :src="item.img" alt="">
            </div>
          </div>
          <arrow :hover="widthHover" :hidden-right="curWTranslateX === -((widthList.length - 2) *wWidth)"
                 :hidden-left="curWTranslateX ===0"
                 @left-click="widthMove(wWidth)"
                 @right-click="widthMove(-wWidth)"/>
        </div>
      </div>
    </div>
  </div>
</template>

